@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=VT323&display=swap');

body {
  margin: 0;
  font-family: 'VT323', monospace;
  -webkit-font-smoothing: none;
  -moz-osx-font-smoothing: grayscale;
  background: black;
}

code {
  font-family: 'VT323', monospace;
}

@keyframes flicker {
  0% { opacity: 0.97; }
  5% { opacity: 0.95; }
  10% { opacity: 0.9; }
  15% { opacity: 0.95; }
  20% { opacity: 1; }
  80% { opacity: 0.9; }
  85% { opacity: 0.95; }
  90% { opacity: 0.93; }
  95% { opacity: 0.94; }
  100% { opacity: 0.98; }
}

@keyframes scanline {
  0% { transform: translateY(0); }
  100% { transform: translateY(100vh); }
}

.crt-effect {
  position: relative;
  overflow: hidden;
}

.crt-corners::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 20px;
  box-shadow: 
    inset 0 0 100px rgba(0, 0, 0, 0.5),
    inset 0 0 40px rgba(0, 0, 0, 0.4);
  pointer-events: none;
}